<template>
  <div class="widgetContainer widgetContainer--scrollable businessDetail">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <p class="title">
          {{ $t('business_profile_header') }}
        </p>
        <span class="el-icon-close" @click="drawerClose" />
      </div>
    </div>
    
    <div class="widgetContainer__body">
      <p class="header-2">
        {{ $t('business_profile_header') }}
      </p>
      <div class="listWithInfo border-1">
        <ul>
          <li>
            <p class="label">
              {{ $t('profile_legalname') }}
            </p>
            <p class="value">
              {{ getSelectedBusiness.legalName }}
            </p>
          </li>
          <li>
            <p class="label">
              {{ $t('profile_dba') }}
            </p>
            <p class="value">
              {{ getSelectedBusiness.dba }}
            </p>
          </li>
          <li>
            <p class="label">
              {{ $t('profile_naicsCode') }}
            </p>
            <p class="value">
              {{ getSelectedBusiness.naicsCode }}
              <br>
              {{ naicsCodeText }}
            </p>
          </li>
        </ul>
      </div>

      <p class="wise-form__header">
        {{ $t('profile_etv') }}
      </p>
      <div class="listWithInfov2 border-1">
        <div v-if="etvValue" class="otherDetails">
          <div v-for="(group, groupName) in etvValue.transactions.annual" :key="`etv-group-${groupName}`">
            <el-row>
              <span class="etv-group-name">{{ $t(`etv_group_${groupName}`) }}</span>
            </el-row>
            <table :style="{ tableLayout: 'fixed', width: '100%' }">
              <tr :key="`etv-group-${groupName}-field-${fieldName}`" v-for="(field, fieldName) in group">
                <td width="100px" class="etv-field-name">
                  {{ $t(`etv_field_${fieldName}`) }}
                </td>
                <td width="50%" class="etv-field-value">
                  {{ field.amount }}
                </td>
                <td class="etv-field-value">
                  {{ field.count }}
                </td>
              </tr>
              <!-- <el-table-column
                v-for="(field, fieldName) in group"
                :key="`etv-group-${groupName}-field-${fieldName}`"
                :label="">
                <template slot-scope="scope">
                  <span v-if="fieldName === 'amount'" class="text-dark">{{ scope.row[fieldName] }}</span>
                  <span v-else-if="fieldName === 'count'" class="text-dark">{{ scope.row[fieldName] }}</span>
                </template>
              </el-table-column> -->
            </table>
          </div>
        </div>
      </div>

      <p class="wise-form__header">
        {{ $t('profile_account_header') }}
      </p>
      <div class="listWithInfov2 border-1">
        <ul>
          <li v-for="a in getAllAccount" :key="a.id">
            <p class="label">
              {{ a.label }}
            </p>
            <p class="value">
              {{ a.accountNumber }}
            </p>
          </li>
        </ul>
      </div>

      <div class="listWithActionsv2">
        <p class="header-2">
          {{ $t('profile_action_header') }}
        </p>
        <ul>
          <li
            @click="helpCenter()"
            v-if="isProfileHelpCenterEnabled">
            <div class="details">
              <p class="label">
                {{ $t('profile_helpcenter') }}
              </p>
            </div>
            <span class="icon-Arrow-small-right icon" />
          </li>
          <li
            @click="openHelp()"
            v-if="isProfileGetInTouchEnabled">
            <div class="details">
              <p class="label">
                {{ $t('profile_getintouch') }}
              </p>
            </div>
            <span class="icon-Arrow-small-right icon" />
          </li>
          <li
            @click="openDisclosures()"
            v-if="isProfileDisclosureEnabled">
            <div class="details">
              <p class="label">
                {{ $t('profile_disclosures') }}
              </p>
            </div>
            <span class="icon-Arrow-small-right icon" />
          </li>
        </ul>
      </div>

      <div class="addressWithInfo">
        <div class="addressWithInfo__header flex-between-center">
          <p class="header-2">
            {{ $t('location') }}
          </p>
          <span @click="editAddress();">
            {{ $t('Edit') }}
          </span>
        </div>

        <div class="addressWithInfo__body">
          <div class="map">
            <GmapMap
              v-if="lat && lng"
              :center="{ lat, lng }"
              :zoom="7"
              map-type-id="terrain"
              style="width: 100%; height: 200px">
              <GmapMarker
                :position="{ lat, lng }"
                :clickable="true"
                :draggable="false" />
            </GmapMap>
          </div>
          <ul>
            <li>
              <span class="label">{{ $t('address') }}</span>
              <span class="value">{{ address }}</span>
            </li>
            <li>
              <span class="label">{{ $t('contact_country_title') }}</span>
              <span class="value">
                {{ getSelectedBusiness.address.country }}
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import { gmapApi } from 'vue2-google-maps';
import { getBrandConfig, getUIConfig } from '@/utils/env';
import { EtvPlainToOption } from '../utills/business';

export default {
  data() {
    return {
      lat: null,
      lng: null,
      isProfileHelpCenterEnabled: getUIConfig('isProfileHelpCenterEnabled'),
      isProfileGetInTouchEnabled: getUIConfig('isProfileGetInTouchEnabled'),
      isProfileDisclosureEnabled: getUIConfig('isProfileDisclosureEnabled'),
      etvValue: null
    };
  },
  computed: {
    ...mapGetters('kyb', ['getSelectedBusiness', 'getNacisCodeData',]),
    ...mapGetters('account', ['getAllAccount']),
    google: gmapApi,
    address() {
      const user = this.getSelectedBusiness;
      const address = user.address;
      if (address.line2 && address.line2.length > 0) {
        return (
          address.line1 +
          ', ' +
          address.line2 +
          ', ' +
          address.city +
          ', ' +
          address.state
        );
      }
      return address.line1 + ', ' + address.city + ', ' + address.state;
    },
    naicsCodeText() {
      const nacisCodeData = this.getNacisCodeData;
      const details = this.getSelectedBusiness;
      if (nacisCodeData.length !== 0 && details) {
        const { naicsCode } = details;
        let selectedItem;
        // eslint-disable-next-line no-plusplus
        for (let i = 0; i < nacisCodeData.length; i++) {
        // eslint-disable-next-line no-plusplus
          for (let j = 0; j < nacisCodeData[i].industries.length; j++) {
            selectedItem = nacisCodeData[i].industries[j].nationalIndustries.find((item) => `${item.code}` === naicsCode);
            if (selectedItem) {
              return `${nacisCodeData[i].name},${nacisCodeData[i].industries[j].name},${selectedItem.name}`;
            }
          }
        }
      }
      return '';
    }
  },
  created() {
    this.getAccountsList();
    this.getLatLong()
    if(this.getNacisCodeData.length === 0) {
      this.fetchNaicsCodeData();
    }
    this.getProjection(this.getSelectedBusiness.id).then((res) => {
      res.transactions.annual = EtvPlainToOption(res.transactions.annual);
      this.etvValue = res;
    });
  },
  methods: {
    ...mapActions('account', ['listAllAccounts']),
    ...mapActions('kyb', ['fetchNaicsCodeData', 'getProjection']),
    getLatLong() {
      this.$gmapApiPromiseLazy().then(() => {
        if (this.google) {
          const geocoder = new this.google.maps.Geocoder();
          const address = this.getSelectedBusiness.address;
          const addressString =
            address.line1 +
            ',' +
            address.line2 +
            ',' +
            address.city +
            ',' +
            address.country;

          geocoder.geocode({ address: addressString }, (results, status) => {
            if (status == this.google.maps.GeocoderStatus.OK) {
              const latitude = results[0].geometry.location.lat();
              const longitude = results[0].geometry.location.lng();
              this.lat = latitude;
              this.lng = longitude;
            }
          });
        }
      });
    },
    helpCenter() {
      const url = getBrandConfig('helpCenter', 'https://intercom.help/solid-2cf7e39d14f4/en/');
      var win = window.open(
        url,
        '_blank'
      );
      win.focus();
    },
    getAccountsList() {
      const loader = this.showLoader();
      this.listAllAccounts(this.getSelectedBusiness.id)
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          loader.close();
        });
    },
    openDisclosures() {
      const url = getBrandConfig('disclosures', 'https://intercom.help/solid-2cf7e39d14f4/en/collections/2921684-legal-disclosures-terms-policies')
      var win = window.open(
        url,
        '_blank'
      );
      win.focus();
    },
    editAddress() {
      this.drawerPush('EditBusinessAddress');
    },
  }
};
</script>
